import React from 'react';
import Link from 'gatsby-link';
import SiteHead from '../../components/SiteHead';
import ExternalLink from '../../components/ExternalLink.js';
import meta from './meta.json';
import methods from './methods.json';
import './index.css';

class DonatePage extends React.Component {
  render() {
    return (
      <div id='donate'>
        <SiteHead
          title={ meta.title }
          description={ meta.description }
          image={ meta.image }
        />

        <div className='header'>
          <h1>Doe para ESXBot</h1>
          <p>
            Apoie o desenvolvimento do ESXBot e mantenha-o funcionando para sempre
            nos doando.
          </p>
          <p>
            Suas doações garantirão o desenvolvimento do <a
            href='https://github.com/ESXBot' target='_blank'
            rel='noopener'>O projeto ESXBot</a> e vamos nos certificar
            este projeto permanece ativo para sempre.
          </p>
          <p>
            Confira as <Link to='/donors'>pessoas que doaram</Link> para
            apoiar o desenvolvimento do projeto.
          </p>
        </div>

        <div className='container'>
          {
            methods.map((method, i) => {
              return (
                <div
                  className='method'
                  key={ i }
                  title={ `${method.title} - ${method.url}` }
                >
                  <ExternalLink to={ method.url }>
                    <div className='image'>
                      <img
                        src={ method.image }
                        width='150'
                        alt='Logotipo do método de doação'
                      />
                    </div>
                    <div className='details'>
                      <p>{ method.description }</p>
                    </div>
                  </ExternalLink>
                </div>
              );
            })
          }
        </div>
      </div>
    );
  }
}

export default DonatePage;



